import request from "../utils/upload-request"; // POST /live/update 文件上传

export function uploadFile(data) {
  console.info('上传的文件', data);
  return request({
    url: '/live/update',
    method: 'post',
    dataType: 'json',
    contentType: false,
    processData: false,
    data: data,
    cache: false // 上传文件不需要缓存

  });
}